@import url("https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@300..900&family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");
*,
*::before,
*::after {
  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

ul[class],
ol[class] {
  list-style-type: none;
}

a {
  color: inherit;
  text-decoration: none;
}
a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

body {
  font-family: "Inter", sans-serif;
  line-height: normal;
}

h1, h2, h3, .font-darker-grotesque, .item-title {
  font-family: "Darker Grotesque", sans-serif;
  line-height: 1;
}

.container {
  max-width: 1600px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
}

/*Components*/
.btn {
  display: inline-flex;
  padding: 10px 32px 14px;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #fff;
  line-height: 1;
  white-space: nowrap;
}

.btn-primary {
  font-family: "Darker Grotesque", sans-serif;
  font-size: 18px;
  font-weight: 600;
  border-radius: 50px;
  background-image: linear-gradient(180deg, #FF7348 0%, #FF7348 51%, #FEAB69 100%);
  background-size: 100% 200%;
  background-position: top center;
  transition-duration: 0.25s;
}
@media (min-width: 1024px) {
  .btn-primary {
    padding: 14px 40px 18px;
  }
}
.btn-primary:hover {
  background-position: bottom center;
}

.btn-option {
  font-size: 16px;
  border-radius: 52px;
  background-color: transparent;
  box-shadow: 0 4px 19px 0 rgba(14, 14, 14, 0);
  color: #2D3335;
}
@media (min-width: 1024px) {
  .btn-option:hover {
    background-color: #FF7348;
    color: #fff;
  }
}
.btn-option:active {
  box-shadow: 0 4px 19px 0 rgba(14, 14, 14, 0.34);
  background-color: #2D3335;
  color: #fff;
}

/*Layout*/
.hero {
  position: relative;
  height: 100vh;
  overflow: hidden;
  background-color: #000;
  z-index: 1;
}
.hero::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 22.81%, rgba(0, 0, 0, 0.75) 70%, #000 76.38%);
  z-index: 0;
}
@media (min-width: 1024px) {
  .hero::after {
    background: linear-gradient(145deg, rgba(0, 0, 0, 0) 15.82%, rgba(0, 0, 0, 0.29) 50.62%, rgba(0, 0, 0, 0.5) 75.65%), linear-gradient(105deg, rgba(0, 0, 0, 0) 31.67%, rgba(0, 0, 0, 0.36) 52.57%, rgba(0, 0, 0, 0.49) 73.82%, rgba(0, 0, 0, 0.9) 98.26%);
  }
}
.hero__text {
  position: absolute;
  left: 22px;
  right: 22px;
  bottom: 56px;
  color: #fff;
  text-align: center;
  font-family: "Darker Grotesque", sans-serif;
  font-size: 18px;
  line-height: normal;
  z-index: 2;
}
@media (min-width: 1024px) {
  .hero__text {
    top: 50%;
    bottom: auto;
    transform: translateY(-50%);
    font-size: 20px;
  }
}
@media (min-width: 1280px) {
  .hero__text {
